const SCREEN_WIDTH = window.innerWidth;
const SCREEN_HEIGHT = window.innerHeight;
const VIEW_ANGLE = 45;
const ASPECT = SCREEN_WIDTH / SCREEN_HEIGHT;
const NEAR = 1;
const FAR = 10000;

let scene;
let camera;
let renderer;
let axisHelper;
let gridHelper;
let geometry;
let material;
let mesh;
let controls;
let pointLight;
let ambientLight;

const origin = new THREE.Vector3(0, 0, 0);

function init() {
  scene = new THREE.Scene();

  gridHelper = new THREE.GridHelper(100, 10);
  scene.add(gridHelper);

  axisHelper = new THREE.AxisHelper(100);
  scene.add(axisHelper);

  camera = new THREE.PerspectiveCamera(VIEW_ANGLE, ASPECT, NEAR, FAR);
  camera.position.set(200, 200, 200);
  camera.lookAt(origin);

  geometry = new THREE.SphereGeometry(1, 2, 2);
  material = new THREE.MeshLambertMaterial({ color: 0xffffff });

  // https://en.wikipedia.org/wiki/Spherical_coordinate_system
  // http://stackoverflow.com/questions/969798/plotting-a-point-on-the-edge-of-a-sphere

  const r = 100;
  const s0 = 0;
  const s1 = Math.PI / 2;
  const t0 = 0;
  const t1 = Math.PI / 2;
  const delta = 0.1;

  for (let s = s0; s <= s1; s += delta) {
    for (let t = t0; t <= t1; t += delta) {
      const x = r * Math.cos(s) * Math.sin(t);
      const y = r * Math.sin(s) * Math.sin(t);
      const z = r * Math.cos(t);
      mesh = new THREE.Mesh(geometry, material);
      mesh.position.set(x, y, z);
      scene.add(mesh);
    }
  }

  ambientLight = new THREE.AmbientLight(0x444444);
  scene.add(ambientLight);

  pointLight = new THREE.PointLight(0xffffff, 1, 1000);
  pointLight.position.set(50, 50, 50);
  scene.add(pointLight);

  renderer = new THREE.WebGLRenderer();
  renderer.setSize(window.innerWidth, window.innerHeight);

  controls = new THREE.OrbitControls(camera, renderer.domElement);

  THREEx.WindowResize(renderer, camera);

  document.body.appendChild(renderer.domElement);
}

function animate() {
  requestAnimationFrame(animate);
  controls.update();
  renderer.render(scene, camera);
}

init();
animate();



